}

@media only screen and (max-width: 767px) {
  .menu {
    float: none;
    position: absolute;
    top: 0;
    left: 100%;
    margin: 0;
    padding: 80px 0 50px;
    width: 100%;
    text-align: center;
    transition: transform 0.5s ease-in-out;
    background-color: #fff;
  }
}

@media only screen and (max-width: 767px) and (max-width: 1023px) {
  .is-open .menu {
    transform: translateX(-100%);
  }
}

.menu li {
  vertical-align: top;
  display: inline-block;
}

@media only screen and (max-width: 767px) {
  .menu li {
    display: block;
  }
}

.menu li:not(:first-child) {
  margin-left: 28px;
}

@media only screen and (max-width: 767px) {
  .menu li:not(:first-child) {
    margin: 20px 0 0;
  }
}

.menu a {
  font-size: 13px;
  font-weight: 700;
  line-height: 1.15385;
  color: #fff;
  transition: color .3s ease-in-out;
}

@media only screen and (max-width: 767px) {
  .menu a {
    color: #000;
  }
}

.no-touch .menu a:hover {
  color: #35A8FF;
}

.btn-menu {
  position: relative;
  float: right;
  margin-top: 19px;
  color: #fff;
  transition: color .5s ease-in-out;
  background-color: transparent;
  display: none;
}

@media only screen and (max-width: 767px) {
  .btn-menu {
    display: block;
  }
}

.is-open .btn-menu {
  color: #000;
}

.btn-menu__text, .btn-menu__wrap {
  vertical-align: top;
  display: inline-block;
}

.btn-menu__text {
  margin-right: 8px;
  font-size: 10px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.08em;
  text-transform: uppercase;
}

.btn-menu__wrap {
  position: relative;
  width: 20px;
  height: 14px;
}

.btn-menu__wrap:before, .btn-menu__wrap:after,
.btn-menu__wrap .btn-menu__line {
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  height: 2px;
  transition: background-color .5s ease-in-out, transform .5s ease-in-out, opacity .5s ease-in-out;
  background-color: #fff;
}

.is-open .btn-menu__wrap:before, .is-open .btn-menu__wrap:after, .is-open
.btn-menu__wrap .btn-menu__line {
  background-color: #000;
}

.btn-menu__wrap:before {
  top: 0;
  transform-origin: top left;
}

.is-open .btn-menu__wrap:before {
  transform: translate3d(4px, -0.5px, 0) rotate(45deg);
}

.btn-menu__wrap:after {
  bottom: 0;
  transform-origin: bottom left;
}

.is-open .btn-menu__wrap:after {
  transform: translate3d(4px, 1px, 0) rotate(-45deg);
}

.btn-menu__wrap .btn-menu__line {
  top: 6px;
}

.is-open .btn-menu__wrap .btn-menu__line {
  transform: translateX(50%);
  opacity: 0;
}

